<template>
    <div>
        <v-row dense center>
            <v-card
            outlined
            >
            <v-list-item three-line>
            <v-list-item-content>
                <div class="text-overline mb-4">
                Chef {{chef.users.name}} {{chef.users.lastName}}'s Profile
                </div>
                <v-list-item-title class="text-h5 mb-1">
                {{chef.title}}
                </v-list-item-title>
                <v-list-item-subtitle>{{chef.summary}}</v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-avatar
                tile
                size="80"
                alt="user"
            >

            <img
                class="elevation-6"
                alt="user"
                :src="$baseUrl+'storage/'+chef.users.avatar"
                :lazy-src="$baseUrl+'storage/'+chef.users.avatar"
            >
            </v-list-item-avatar>
            </v-list-item>
            </v-card>

        </v-row>


      <v-row dense center class="mt-6">
        <v-col
            v-for="(meal, index) in meals"
            :key="index"
            xs="12"
            sm="2"
            md="2"
            lg="2"
            xl="2"
        >
            <v-hover >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        v-if="index>=0"
                        hover
                    >
                        <v-img
                            v-if="meal.meal_file"
                            :src="$baseUrl+'storage/'+meal.meal_file.path+meal.meal_file.name"
                            :lazy-src="$baseUrl+'storage/'+meal.meal_file.path+meal.meal_file.name"
                            aspect-ratio="1"
                            class="white--text align-end"
                            gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                            height="200px"
                            @click.stop="open('pub-meal/'+meal.key)"
                            >
                            <template v-slot:placeholder>
                                <v-row
                                    class="fill-height ma-0"
                                    align="center"
                                    justify="center"
                                >
                                    <v-progress-circular
                                    indeterminate
                                    color="grey lighten-5"
                                    ></v-progress-circular>
                                </v-row>
                            </template>

                            <p class="ml-3 white--text font-weight-light text-truncate">
                            {{meal.name}}
                            </p>
                        </v-img>
                    </v-card>
            </v-hover>
        </v-col>

      </v-row>
    </div>
</template>

<script>
  export default {


    components: {

    },

    data() {
        return {
            chef:'',
            fab: false,
            drawer:false,
            meals:[],
            loading: false,
            selection: 1,
        }

    },

    computed: {
        token(){
            return this.$route.params.token;
        },
    },
    methods: {
        open(url){
            let routeData = this.$router.resolve({path: url});
            var route = routeData.href.replace("pub-chef-meals/", "");
            window.open(route, '_blank');

        },
    },
    mounted() {
    this.axios
        .get('pub-chef-meals-data/'+this.token)
        .then(response => (this.meals = response.data.data, this.chef = response.data.cv ));
    },

  }
</script>

